import http from '../http'

export function getDataBank() {
  return http.get('admin/bank')
}
export function postDataBank(data) {
  return http.post('admin/bank', data)
}
export function deleteDataBank(id) {
  return http.delete('admin/bank/'+id)
}
export function putDataBank(id, data) {
  return http.post('admin/bank/'+id, data)
}