<template>
  <div>
    <v-btn
      depressed
      dark
      @click="pesan"
      class="abp-color">
      pesan
    </v-btn>
    <v-dialog
      class="d-print-none"
      v-model="dialog"
      width="500"
      persistent>
      <v-card>
        <v-card-text class="pt-4">
          <div class="title">
            Apakah anda yakin ingin mengubah paket yang sudah ada ?
          </div>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            color="primary"
            @click="pesan"
            depressed>
            pesan
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        dialog: false,
      }
    },
    methods: {
      pesan(val) {
        const token = window.localStorage.getItem('token_abp')
        if (token === null) {
          window.localStorage.setItem('id_paket', val)
          this.$router.push({ name: 'register-incubator' }) 
        } else {
          this.dialog = true
        }    
      }
    }
  }
</script>