import http from '../http.js'

export function postDataPesan(id) {
  return http.post('incubator/pesan-paket', id)
}

export function postDataKonfirmasiPembayaran(data) {
  return http.post('incubator/konfirmasi-pembayaran', data)
}

export function getDataRiwayatPembayaran(data) {
  return http.post('incubator/riwayat', data)
}

