import {
	getDataBank,
	postDataBank,
	putDataBank,
	deleteDataBank
} from '@/config/admin/bank'

const dataBank = {
	namespaced: true,
	state: {
		dataBank: [],
		isLoading: false,
		isLoadingBtn: false,
		status: null
	},
	mutations: {
		updateDataBank(state, payload) {
			state.dataBank = payload
		},
		updateStatus(state, payload) {
			state.status = payload
		},
		updateIsLoading(state, payload) {
			state.isLoading = payload
		},
		updateIsLoadingBtn(state, payload) {
			state.isLoadingBtn = payload
		}
	},
	actions: {
		updateIsLoading(context, payload) {
			context.commit('updateIsLoading', payload)
		},
		updateIsLoadingBtn(context, payload) {
			context.commit('updateIsLoadingBtn', payload)
		},
		getDataBank(context) {
			getDataBank()
				.then(res => {
					if (res.status === 200) {
						context.commit('updateDataBank', res.data.data[0])
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		postDataBank(context, payload) {
			postDataBank(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataBank')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		},
		putDataBank(context, payload) {
			putDataBank(payload.id, payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataBank')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoadingBtn', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoadingBtn', false)
				})
		},
		deleteDataBank(context, payload) {
			deleteDataBank(payload)
				.then(res => {
					if (res.status === 200) {
						context.dispatch('getDataBank')
						context.commit('updateStatus', true)
						context.dispatch('updateIsLoading', false)
					}
				})
				.catch(() => {
					context.commit('updateStatus', false)					
					context.dispatch('updateIsLoading', false)
				})
		}
	}
}

export default dataBank